.section_education {
    padding: 20px 40px;
    background-color: #b98119;
}
.container_education {
    display: flex;
    padding: 10px;
    background-color: rgba(210,105,30, 0.8);
    border-radius: 15px;
    justify-content: center;
    margin: auto;
    margin-bottom: 30px;
    width: 900px;
    box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, 
    rgba(0, 0, 0, 0.09) 0px 4px 2px, 
    rgba(0, 0, 0, 0.09) 0px 8px 4px, 
    rgba(0, 0, 0, 0.09) 0px 16px 8px, 
    rgba(0, 0, 0, 0.09) 0px 32px 16px;
}
.container_image {
    display: flex;
    justify-content: center;
}
.text_group {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.education_title {
    color: black;
    font-weight: 800;
    font-size: 18px;
}
.education_text {
    color: darkred;
    font-weight: 700;
    font-size: 17px;
}

.sertificate {
    display: flex;
    padding: 20px;
    justify-content: center;
}

@media screen and (min-width: 1024px) {
    .container_education {
        width: 800px;
    }
    .text_group {
        margin-left: 20px;
    }

}

@media screen and (max-width: 1024px) {
    .container_education {
        display: block;
        width: 400px;
    }
    
}

@media screen and(max-width: 800px) {
    .container_education {
        width: 400px;
    }

}

@media screen and (max-width: 480px) {
    .container_education {
        width: 300px;
    }
    .education_text {
        margin-left: 10px;
    }
    .text_group {
        padding: 20px;
    }

}

@media screen and (max-width: 350px) {
    .container_education {
        width: 280px;
    }
}