.btn-profile {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    padding: 14px 0;
    width: 160px;
    font-weight: 600;
    cursor: pointer;
    transition: 0.2s;
    margin-right: 5px;
    /* outline: none;
    border: none; */
  }
  
  .btn-profile:active {
    transform: translateY(2px);
  }
  
  .primary-btn-profile {
    border: 2px solid rgb(219, 115, 12);
    background-color:rgb(225, 145, 65);
    font-size: 12px;
    margin-right: 20px;
  }
  .profile-details-name p {
    font-size: 34px;
    line-height: 63px;
    color: transparent;
    font-weight: 700;
    text-transform: uppercase;
    color: #041c34;
    -webkit-text-stroke: 1px #fff;
    text-shadow: 0px 0px 50px #3aaef77a;
  }
  .primary-btn-profile:hover {
    color: aliceblue;
    border: 2px solid #072e77;
    background-color: #3d78e5;
  }
  
  .highlighted-btn-profile {
    font-size: 12px;
    color: black;
    border: 2px solid blue;
    background-color: #0e55d8;
  }

  .highlighted-btn-profile:hover {
    border: 2px solid orangered;
    color: white;
    background-color: rgb(215, 96, 5);
  }
  
.profile-container,
.profile-parent {
  display: flex;
  align-items: center;
  text-align: center;
  /* background-color: bisque; */
  padding: 5px;
}
.profile-container {
  justify-content: flex-start;
  flex-direction: column;
  position: sticky;
}
.profile-parent {
  width: 90%;
  text-align: left;
  justify-content: center;
}
.profile-details-name {
  font-size: 30px;
  line-height: 40px;
}
.profile-details-role {
  display: flex;
  flex-direction: column;
  margin: 15px 0 18px 0;
}

.profile-details-role h1 {
    font-size: 24px;
    line-height: 32px;
    margin: 0 0 6px 0;
    /* text-transform: uppercase; */
    text-shadow: 0px 0px 50px rgba(0, 133, 234, 0.5);
}
  
  .colz {
    padding: 12px 0px;
}
  .primary-text {
    color: (--white);
    font-size: 24px;
}
  
  .highlighted-text {
    text-shadow: 0 0 9px #1d1122;
    color: coral;
    margin-right: 9px;
    margin-left: 6px;
    font-weight: 400;
}
  .profile-role-tagline {
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    margin: 5px 0 0 0;
    color: darkslateblue;
}
  .profile-options .highlighted-btn {
    margin: 0 0 0 28px;
}
  .profile-options .highlighted-btn:hover {
    margin: 0 0 0 28px;
    background-color: cornsilk;
    color: #111;
}
  .profile-picture {
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 3px 0px 0px #ffffff;
    height: 400px;
    width: 400px;
    border-radius: 50%;
}
