.colz .colz-icon i {
    color: #fff;
    padding: 8px;
    padding-left: 0px;
    font-size: 18px;
}
  
  .colz .colz-icon i:hover {
    transform: scale(1.5);
}

.profile-picture-background:hover {
    animation: bounce_one 1s infinite;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 0.15s;
}

@media screen and (min-width: 1024px) {
    .profile-details {
      width: 500px;
}
    .profile-parent {
      height: 100vh;
      padding-bottom: 100px;
}
    .profile-picture {
      margin: 0 0px 0 80px;
}
    .profile-details-name {
      font-size: 20px;
}
    .profile-details-role .primary-text {
      font-size: 12px;
}
    .profile-options {
       display: flex;
       margin-top: 10px;
}
    .profile-options .btn {
      width: 120px;
      padding: 11px 0;
      z-index: 700;
      height: 47px;
      color: #fff;
    }
}

@media screen and (max-width: 1024px) {
     .profile-details {
    width: 400px;
    }
    .profile-details-name p {
      font-size: 24px;
      line-height: 54px;
    }
    .profile-parent {
      height: 100vh;
    }
    .profile-picture {
      display: none
    }
    .profile-options {
      display: flex;
      justify-content: space-around;
      margin-top: 10px;
    }
}
  @media screen and(max-width:910px) {
    .profile-picture {
      height: 320px;
      width: 320px;
      margin: 0 0 0 4px;
    }
}
  @media screen and (max-width: 810px) {
    .profile-container {
      width: 100%;
      background-color: #050517;
      padding: 18px 0 21px 0;
      background-size: cover;
      clip-path: polygon(100% 0, 100% 96%, 54% 100%, 0 94%, 0 0);
}
    .profile-picture {
      margin-bottom: 30px;
      display: flex;
}
    .profile-details-role h1 {
      height: 100px;
      text-align: center;
      display: none !important;
}
    .profile-details-name p {
      font-size: 24px;
      line-height: 3px;
      text-decoration-color: blueviolet;
}
    .profile-parent {
      flex-direction: column-reverse;
      margin: 40px 0 0 0;
      width: 90%;
      height: 100% !important;
      justify-content: center;
      text-align: center;
}
   .profile-role-tagline {
    /* width: 440px; */
    padding: 5px;
   }
    .profile-options {
      display: flex;
      justify-content: space-around;
      margin-top: 10px;
}
    .profile-deails {
      display: flex;
      margin-top: 15px;
      margin: 25px 0 0;
      justify-content: center;
      text-align: center;
}
    .primary-text {
      line-height: 24px;
}
    .colz .colz-icon i {
      color: #ffffff;
      text-shadow: 0px 0px 1px #ffffff;
}
    .profile-options .highlighted-btn {
      margin: 0 !important;
}
    .profile-options button {
      font-size: 14px !important;
}
    .profile-options,
    .profile-details-role {
      text-align: center;
}
    .profile-options {
      padding-bottom: 31px;
    }
}

 